<script>
import orderDescTabs from '../components/orderDesc/orderDescTabs.vue'
export default {
    data() {
        return {
            number: '',
            outerVisible: false,
            innerVisible: false,
            innerVisible2: false,
            innerVisible3: false,
            innerVisible4: false,
            innerVisible5: false,
            offerVisible: false
        }
    },
    computed: {
        orderDesc() {
            let desc = localStorage.getItem('ORDER_DESC')
            return JSON.parse(desc)
        }
    },
    created () {
        this.number = this.$route.params.id
    },
    methods: {
        
        //
        handleCommand(command) {
            if(command == 'offer') {
                this.offerVisible = true
            }
        },
        // 
        hanleReturn() {
            this.$router.go(-1)
        }
    },
    components: {
        orderDescTabs,
    }
}
