            width: 80px;
        }
        input {
            width: 60%;
            outline: none;
            padding-left: 6px;
            border-radius: 4px;
            border: 1px solid #ddd;
            line-height: 25px;
        }
    }
}
.operation {
    text-align: right;
}
.order-desc-container {
    padding: 53px 0 0 55px;
    background-color: #fff;
    height: 1000px;
    box-sizing: border-box;
    header {
        margin: 20px 20px 0 20px;
        padding-bottom: 15px;
        border-bottom: 1px solid #eee;
        h3 {
            font-size: 20px;
            font-weight: normal;
        }
        .el-button+.el-button {
            margin-left: 0;
        }
    }
    .main {
        height: 100%;
        padding: 0 20px 20px 20px;
        .content {
            width: 19%;
            height: 100%;
            border-right: 1px solid #eee;
            box-sizing: border-box;
            .title {
                margin: 15px 15px 0 0px;
                position: relative;
                .icon, span {
                    font-size: 18px;
                    font-weight: 700;
                }
                .status {
                    text-align: center;
                    font-size: 13px;
                    font-weight: 300;
                    line-height: 21px;
                    position: absolute;
                    width: 50px;
                    height: 21px;
                    background-color: #ff9900;
                    color: #fff;
                    border-radius: 4px;
                    top: 0;
                    right: 0;
                }
            }
            .desc {
                margin: 20px 15px 20px 0;
                border-top: 1px dashed #ccc;
                border-bottom: 1px dashed #ccc;
                ul li {
                    font-size: 14px;
                    margin: 10px 0;
                    span {
                        font-weight: 700;
                        line-height: 2em;
                    }
                }
            }
        }
        .tabs {
            width: 81%;
            height: 100%;
        }
    }
    
    .approval, {
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        padding: 20px 100px;
        ul li {
            margin-bottom: 20px;
            span {
                margin-right: 20px;
            }
            select {
                width: 70%;
                height: 26px;
                padding-left: 10px;
            }
            button {
                background-color: rgba(204, 204, 204, 1);
                border: 0;
                outline: none;
            }
            table {
                width: 80%;
                text-align: center;
                margin-left: 80px;
                margin-top: 10px;
                border: 1px solid #ddd;
                th {
                    font-weight: normal; 
                    padding: 4px 0;                              
                }
                td {
                    padding: 4px 0;
                    button {
                        background-color: #fff;
                        border: 1px solid #ddd;
                        padding: 4px;
                    }
                }
            }
        }
    }
    .offer {
        padding:20px;
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        .title {
            margin-top: 10px;
            background-color: #f2f2f2;
            height: 35px;
            line-height: 35px;
            padding: 0 10px;
            font-weight: 700;
            border: 1px solid #ddd;
            border-bottom: none;
            i {
                color: #f00;
            }
            button {
                padding: 5px;
                margin: 4px 8px 0 0;
            }
        }
        table {
            width: 100%;
            text-align: center;
            border: 1px solid #ddd;
            margin-bottom: 20px;
            font-size: 14px;
        }
    }
}
</style>




