<template>
    <div class="table-container">
        <el-button type="primary" size="small" icon="el-icon-plus" @click="dialogFormVisible = true"></el-button>
        <el-table
        :data="tableData"
        style="width: 100%;font-size:12px;margin-top:10px;"
        >
        <el-table-column
            prop="projectNo"
            label=""
            width="80px">
        </el-table-column>
        <el-table-column
            prop="name"
            label="">
        </el-table-column>
        <el-table-column
            prop="type"
            label="">
        </el-table-column>
        <el-table-column
            prop="preservingState"
            label="">
        </el-table-column>
        <el-table-column
            prop="firstParty"
            label="">
        </el-table-column>
        <el-table-column
            prop="preservingGrade"
            label="">
        </el-table-column>
        <el-table-column
            prop="preservingBeginTimes"
            label=""
            :formatter="formatTime">
        </el-table-column>
        <el-table-column
            prop="preservingEndTimes"
            label=""
            :formatter="formatTime">
        </el-table-column>
        <el-table-column
            prop="contractPrice"
            label="">
        </el-table-column>
        <el-table-column
            prop="contacts"
            label="">
        </el-table-column>
        <el-table-column
            prop="contactsTel"
            label="">
        </el-table-column>
        <el-table-column
            label="">
            <template slot-scope="scope">
                <el-button type="text" size="small" @click="handleClickDesc(scope.row)"></el-button>
                <el-button type="text" size="small" @click="deleteProject(scope.row)"></el-button>
            </template>
        </el-table-column>

        </el-table>

        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.type" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.preservingState" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.firstParty" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.preservingGrade" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-date-picker
                    v-model="form.beginTime"
                    type="date"
                    placeholder=""
                    value-format="timestamp">
                </el-date-picker>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-date-picker
                    v-model="form.endTime"
                    type="date"
                    placeholder=""
                    value-format="timestamp">
                </el-date-picker>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.contractPrice" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.contacts" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.contactsTel" auto-complete="off"></el-input>
                </el-form-item>
                
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="addProject"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            pageSize: 8,
            offset: 1,
            form: {
                id: '',
                contacts: '',
                contactsTel: '',
                firstParty: '',
                name: '',
                beginTime: '',
                endTime: '',
                preservingGrade: '',
                projectNo: '',
                type: '',
                contractPrice: '',
                preservingState: ''
            },
            dialogFormVisible: false,
            formLabelWidth: '150px'
        }
    },
    created() {
        this.findAllProject()
    },
    methods: {
        //
        handleClickDesc(row) {
            this.$router.push({ path: `/project/desc/${row.id}` })
        },
        //  
        addProject() {
            this.form.beginTime = this.form.beginTime / 1000
            this.form.endTime = this.form.endTime / 1000
            let params = this.$qs.stringify(this.form)
            this.$http({
                url: 'omm/projectInfo/saveProject',
                data: params
            }).then( res => {
                if( res.data.success == true ) {
                    this.findAllProject()
                }
            } )
            this.dialogFormVisible = false
        },
        //
        findAllProject () {
            let params = {
                pageSize: this.pageSize,
                offset: this.offset
            }
            params = this.$qs.stringify(params)
            this.$http({
                url: 'omm/projectInfo/findAll',
                data: params
            }).then( res => {
                if( res.data.success == true ) {
                    let proArr = res.data.obj
                    proArr.forEach( (item, index) => {
                        item['projectNo'] = index + 1
                    })
                    this.tableData = proArr
                    console.log(proArr)
                }
            } )
        },
        //
        deleteProject(row) {
            let params = {
                id: row.id
            }
            params = this.$qs.stringify(params)
            this.$http({
                url: 'omm/projectInfo/delete',
                data: params
            }).then( res => {
                if( res.data.success == true ) {
                    this.findAllProject()
                }
            } )
        },
        // 
        formatTime (row, column, timestamp) {
            var date = new Date(timestamp * 1000 );
            var Y = date.getFullYear() + '-';
            var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
            var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
                return Y+M+D;
        }
    }
}
</script>

<style lang="less" scoped>
.table-container {
    padding: 25px 20px 20px 15px;
}
.el-form-item  {
    width: 500px;
}

</style>





