    }
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        display: none;
        width: 520px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 99;
        span {
            font-size: 15px;
        }
        ul li {
            float: left;
            width: 50%;
            font-size: 14px;
            line-height: 30px;
        }
    }
}
</style>




