<template>
    <div class="tabs-container">
        <el-tabs v-model="activeName2" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
                <HandleProcess />
            </el-tab-pane>
            <el-tab-pane label="" name="second"></el-tab-pane>
            <el-tab-pane label="" name="third"></el-tab-pane>
            <el-tab-pane label="" name="fourth"></el-tab-pane>
            <el-tab-pane label="" name="fifth"></el-tab-pane>
            <el-tab-pane label="" name="sixth">
				<HandleProjectDesc />
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
import HandleProcess from './handleProcess.vue'
import HandleProjectDesc from './handleProjectDesc.vue'
  export default {
    data() {
      return {
        activeName2: 'first'
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
    },
    components: {
        HandleProcess,
		HandleProjectDesc,
    }
  }
</script>
<style lang="less" scoped>
.tabs-container {
    margin: 20px 0 0 20px;
}
</style>




